.customize-control-headline {
    margin-top: 20px;

    > div {
        margin: 0 -12px 0 -12px;
        padding: 10px 12px;
        background: white;
        border-top: 1px solid #dedede;
        border-bottom: 1px solid #dedede;

        &.customize-control-headline-without-bg {
            background: none;
        }

        > h1,
        > h2,
        > h3,
        > h4,
        > h5 {
            margin: 0;
        }

        > span {
            margin: 5px 0 0 0;
        }
    }
}

.customize-control-customHtml[id$="-pseudo"] {
    margin: 0;

    & + .customize-control-headline {
        margin-top: 0;
    }
}
