.customize-control-rangeInput .customize-control-content {
    display: flex;

    > div {
        position: relative;
        width: 120px;
        margin-left: 15px;

        > input {
            text-align: right;
            padding-right: 30px;

            &::-webkit-outer-spin-button,
            &::-webkit-inner-spin-button {
                -webkit-appearance: none;
                -moz-appearance: textfield;
                margin: 0;
            }
        }

        &[data-unit]::after {
            content: attr(data-unit);
            position: absolute;
            top: 50%;
            margin-top: -7px;
            right: 10px;
            font-size: 14px;
            line-height: 1;
            text-align: left;
            width: 20px;
        }
    }
}
